import React from 'react'
import { FaTimes } from 'react-icons/fa';
import styles from '../Styles/Market.module.css'
import {useState, useMemo} from "react"
import '../Styles/Reponsive.css'

function Market() {
   const [order, setOrder] = useState(1)
    const getLocal = JSON.parse(localStorage.getItem('products')) 
    const [getItem, setItem2] = useState(getLocal ?? []) 
    const total = useMemo (()=>{
       const result =  getItem.reduce((a,b) => {
            return a+b.price
        },0)
        return result
    },[getItem])
    const handleDelete =  (i) => {
            setItem2((prev)=> {
                const a = [...prev]
                a.splice(i,1)
                localStorage.setItem('products', JSON.stringify(a))
                return a
            })
    } 
    const handleIncrease = () => {
        setOrder(order + 1)
    }
    const handleDecrease = () => {
        if (order <= 0) {
            setOrder(0)
        } else
            setOrder(order - 1)
    }
    return (
        <div className="containermarket">
            <div className={styles.market, "market"}>
            {getItem.map((item,i) => 
                 <div key={i}className={styles.market_body}>
                 <img className={styles.market_img} src={item.url} alt="img"></img>
                 <div className={styles.marketitem}>
                     <p className={styles.markettext2}>{item.title}</p>
                     <p className={styles.markettext}>{item.price}đ</p>

                     <div className={styles.market_btn}>
                         <button className={styles.marketbtnincrease} onClick={handleDecrease}>-</button>
                         <p>{order}</p>
                         <button className={styles.marketbtnincrease} onClick={handleIncrease}>+</button>
                     </div>
                 </div>

                 <div className={styles.market_footer}>
                 <div onClick={()=>handleDelete(i)}>
                 <span className={styles.time}><FaTimes /></span>
                 </div>
                     <p className={styles.markettext2}>{item.price}đ</p>
                     
                 </div>
                 

             </div>
            )}
               
                    
                <span className={styles.hr}></span>
                <div className={styles.markettotal}>
                    <div className={styles.total}>
                        <p className={styles.tong}>Tổng cộng:</p>
                        <p className={styles.totalprice}>{total}đ</p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Market